import React from "react"
import { Link } from "gatsby"

import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"


const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`

  const data = useStaticQuery(graphql`
  query Layout {
    avatar: file(absolutePath: { regex: "/logo_transparent.png/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author {
          name
          summary
        }
        social {
          twitter
        }
      }
    }
  }
`)

  const Nav = () => (
    <nav class="flex items-center justify-between flex-wrap bg-black px-8 pt-4">
    <div class="flex items-center flex-shrink-0 text-white mr-6">
    <a href="/" >
      <Image
        fixed={data.avatar.childImageSharp.fixed}
      /></a> 
    </div>
  </nav>
  )
  let header

  if (location.pathname === rootPath) {
    header = (
      <header class="bg-black">
      <div className="container p-8 pt-32 pb-16">
      <div class="text-white leading-none text-6xl tracking-wide">
        Hi there! I am Jiahui Lum and I develop web applications.</div>
        </div>
      </header>
    )
  } else {
    header = (
      <h3
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  return (
    <>
   {Nav()}
   <header>{header}</header>
      <main>{children}</main>
    
    </>
  )
}

export default Layout
